.root {
	padding-top: 10px;
	padding-left: 10px;
	padding-bottom: 50px;
	overflow: scroll;
	height: 90vh;
	display: flex;
	justify-content: space-around;

	.oneDiv {
		flex-grow: 1;
	}

	.twoDiv {
		flex-grow: 2;
	}

	.treeDiv {
		flex-grow: 1;
	}

	
		 button {
			margin-top: 20px;
			margin-right: 20px;
			margin-bottom: 40px;
		}
	

	div {
		.headerTable {
      padding-left: 5px;
			background-color: #bfb433;
			display: flex;
			justify-content: start;

			& > div {
				font-size: calc( (100vw - 30rem)/(80 - 30) * (1.5 - 1) + 1rem);
				width: 25%;
			}
		}
	}
}
